body {
 
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #282c34;
  width: 100vw;
  height: auto;
  overflow-x: hidden;
}
@import url('https://fonts.googleapis.com/css2?family=Cardo&family=EB+Garamond&family=Markazi+Text&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Bitter:wght@700&family=Cinzel:wght@700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Inconsolata&display=swap');

@import url('https://fonts.googleapis.com/css2?family=Hind+Guntur:wght@500&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Teko:wght@500&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Comic+Neue:wght@700&display=swap');


:root {
  --bg: #000000;
  --bgl:#333;
  --color: #ffffff;
  --white: white; 
  --chart-1: #00FAB6;
  --chart-2: #FCF3CF;
 
  
  --text-highlight: #64ffda;
  --body-highlight: #64ffda;

  --text-highlight-2: lime;
  
  --fullwidth: 100%;
  --theme-ui-colors-purple: #6900FF;
  --theme-ui-colors-green: #5CD786;
  --theme-ui-colors-yellow: #E9DD1E;
  --theme-ui-colors-pink:#f64747;
  --theme-ui-colors-cyan: #64ffda;



}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
