
 .abt1{
   position: relative;
   height: auto;
 } 
  .leftcolumn {
    
      float: left;
      width: 30%;
      height: 100vh;  
      text-align: left;
      font-family: 'Teko', sans-serif;
       
    }
 
  .leftcolumn > h1{
   
    margin-left: 20%;
    padding-bottom: 0;
    margin-bottom: 5%;
    margin-top: 20%;
     font-size: calc(30px + 2vmin); 

  }

  .profile-image{
   position: relative;
    border-radius: 8px;
    top: 5px;
     margin: 15% 20%;
     border: 2px solid  var(--color);
  }
  
  .profile-image:hover{

  animation: jumb 2s linear infinite ;
  }
  
  @keyframes jumb{
    0%, 30%, 60%, 100%{
      transform: translate(-2px, -2px) rotate(2deg);
    }
    15%, 50% {
      transform: translate(5px, 4px) rotate(4deg);
    }
  }
    
    /* Right column */
   .rightcolumn {   
      text-align: left;
      float: right;
      width: 70%;
      height:100vh;
      
      font-family: 'Inconsolata', monospace;
      letter-spacing: 1px;
        font-size: calc(12px + 1vmin);
    }

  
  
  .card-c2{
    position: relative;
    padding-left: 3%;
    
      top:30%;
      position: relative;
  
      width:80%;
      line-height: 30pt;


  }
  .ecgIcon{
    position: relative;
    top : 55%;
    width: 3.5em;
    height: 3.5em;
  }



  .indiaIcon{
     
      width: 1.5em;
      height: 1.5em;
      animation: float 4s infinite;
      transform-origin: top left;
    
  }

  @keyframes float{
    0%,100%{
      
      transform: rotateY(45deg);
    }
    50%{
      transform: rotateY(0deg)
    }
  }



 

   


  /* .ecgIcon path {
    stroke : yellow;
    
  } */

  
.ambulance{
  position: absolute;
 display: flex;
 
  font-size: calc(18px + 1vmin);
 
 
  /* transform-origin: top left; */
  animation: run 10s linear  infinite;
  animation-direction: initial;
  animation-fill-mode: forwards;
}
  
@keyframes run {
0%{
  right: 0%;

}

100%{
  right: 100%;
}
    
  



   
}




 


  .footer{
 position: absolute;
    width: 100vw;
    height: 30px;
    background-color: lawngreen;
    bottom: 0px;
  }


  /* Responsive layout - when the screen is less than 800px wide, make the two columns stack on top of each other instead of next to each other */
@media screen and (max-width: 800px) {
    
  .abt1{
    position: relative;
    height: auto;
  } 
  
  
  .leftcolumn {
    position: relative;
      width: 100%;
      height:40vh;  
    }



    .leftcolumn > h1{
      position: absolute;
      bottom: 10%;
     
      padding: 0;
      margin: 0;
      margin-left: 10%;
    }

    .profile-image{
      position: absolute;
     height: 25vmax;
    
     right: 10%;
     margin: 15% 0;
     padding:0;
     bottom: 0%;
    
     }

    .rightcolumn{
     position: relative; 
     width:100%;
     height: auto;  
    }

    .card-c2{
      position: relative;
      width:90%;
     
      /* padding-top: 10%; */
      /* top:20%; */
      margin: auto;
      align-content: center;
      top:  3%;
     height: auto;
    }

    .ambulance{
      animation: run 6s linear  infinite;
    }
}
