.contact{
  position: relative;
  height: 100vh;
}


.contact-b1{
position: absolute;
}

.contact-h1{

position: absolute;
border-bottom: 15px solid var(--bgl);
border-right: 15px solid transparent;

top: 10vh;
width: 70vw;
 
}

.contact-ht1{
position: absolute;
font-family: 'Teko', sans-serif;
font-size: calc(25px + 2vmin); 
top: 4.5vh;
text-shadow: black;
letter-spacing: 1px;

margin: 10px;
margin-left: 20px;
}
/* .contact-tr1{
position: absolute;
border-bottom: 50px solid var(--bgl);
border-left: 30px solid transparent;



} */
.contact-l1{
position: relative;
width:100vw;

}

.social-links{

position: relative; 
top : 20vh;
height: 25vh;
width: 35%;

display: flex;
margin: auto;
justify-content: center;
}

.social-links > .link{
width: 50px;
height: 50px;
margin: auto;
display: flex;
justify-content: center;
}

.social-links > .link > .icon:nth-child(1){
fill: white;
width: 46px;
height: 46px;
}

.social-links > .link:nth-child(4) > .icon{
width: 43px;
height: 43px;  
}

.social-links > .link:hover > .icon{
padding: 4px;
}


.social-links > .link:nth-child(1):hover > .icon{
fill: var( --theme-ui-colors-purple);
}
.social-links > .link:nth-child(2):hover > .icon{
fill: var( --theme-ui-colors-yellow);
}
.social-links > .link:nth-child(3):hover > .icon{
fill: var( --theme-ui-colors-pink);
}
.social-links > .link:nth-child(4):hover > .icon{
fill: var( --theme-ui-colors-green);
}

.thanks{
position: relative;
text-align: center;
width: 100%;

top: 25vh;
font-family: 'Hind Guntur', sans-serif;
}
.thanks > .mobile{
color: var(--text-highlight-2);

} 

.thanks > .desktop {
display: none;
}

.contact-footer{
position: absolute;
padding:2%;
bottom: 0px;
font-family: 'Comic Neue', cursive;

}

.contcat-cv-download{
display: none;
}

@media screen and (max-width: 800px){

.social-links{
  top : 15vh;
  width: 90vw;
  height: 20vh;
}

.social-links > .link > .icon:nth-child(1){
  
  width: 35px;
  height: 35px;
}

.social-links > .link:nth-child(4) > .icon{
  width: 34px;
  height: 34px;  
}



.thanks > .mobile{
  display: none;
} 

.thanks > .desktop {
  color: var(--text-highlight-2);
  display: inline;
}


.thanks{
  width:85vw;
  text-align: left;
  margin:10% 5%;
  z-index: 1;
}
.contcat-cv-download{
  font-family: 'Raleway', sans-serif;
  position: relative;
  top:  18vh;
  width: 50%;
  
  color: var(--color);
  margin: auto;
 
  border: solid 1px;
  padding: 10px 10vw;
  text-align: center;
  text-decoration: none;
  display: flex;
  justify-content: center;
}

.contcat-cv-download:hover{
  background-color: var(--body-highlight);
  color: var(--bg);
}

}

@media screen and (max-width: 750px)
and (orientation: landscape){
  .contact{
   
    height: 150vh;
  }
  
  .contact-l1{
    top: 10%;
  }
}