}

.fullStack > span:nth-child(1) {
	font-size: 0px;
	-webkit-animation: roll1 ease-in-out 6s infinite;
	        animation: roll1 ease-in-out 6s infinite;
}

.fullStack > span:nth-child(2) {
	font-size: 0px;
	-webkit-animation: roll2 ease-in-out 6s infinite;
	        animation: roll2 ease-in-out 6s infinite;

}

.fullStack > span:nth-child(3) {
  -webkit-animation: roll3 ease-in-out  6s infinite;
          animation: roll3 ease-in-out  6s infinite;
}


@-webkit-keyframes roll1 {
  0%, 30%{
		opacity: 1;
		font-size: inherit
	}
	30%, 60%{
		opacity: 0;
		font-size: 0
	}
	60%, 100%{
		opacity: 0;
		font-size:0px;
	}
}


@keyframes roll1 {
  0%, 30%{
		opacity: 1;
		font-size: inherit
	}
	30%, 60%{
		opacity: 0;
		font-size: 0
	}
	60%, 100%{
		opacity: 0;
		font-size:0px;
	}
}

@-webkit-keyframes roll2 {
	0%, 30%{
		font-size:0px;
		opacity: 0;
	}
  30%, 60%{
		opacity: 1;
		font-size: inherit
	}
	60%, 100%{
		opacity: 0;
		font-size:0px;
	}
}

@keyframes roll2 {
	0%, 30%{
		font-size:0px;
		opacity: 0;
	}
  30%, 60%{
		opacity: 1;
		font-size: inherit
	}
	60%, 100%{
		opacity: 0;
		font-size:0px;
	}
}

@-webkit-keyframes roll3 {
	0%, 30%{
		opacity: 0;
		font-size:0px;
	}
  30%, 60%{
		opacity: 0;
		font-size:0px;
	}
	60%, 100%{
		opacity: 1;
		font-size:inherit
	}
}

@keyframes roll3 {
	0%, 30%{
		opacity: 0;
		font-size:0px;
	}
  30%, 60%{
		opacity: 0;
		font-size:0px;
	}
	60%, 100%{
		opacity: 1;
		font-size:inherit
	}
}

.flicker:hover{
  -webkit-animation: upper 2s linear;
          animation: upper 2s linear;
}


@-webkit-keyframes upper {
  0%, 30% {
		
    opacity:.99; 
  }
  30%, 60% {
    opacity:0.5; 
    font-size: 70px;
    color: yellow;

  }
  60%, 100%{
		font-size:inherit;
		text-shadow:inherit;
		opacity:1;
  }
}


@keyframes upper {
  0%, 30% {
		
    opacity:.99; 
  }
  30%, 60% {
    opacity:0.5; 
    font-size: 70px;
    color: yellow;

  }
  60%, 100%{
		font-size:inherit;
		text-shadow:inherit;
		opacity:1;
  }
}
 
