    overflow: hidden;

    display: flex;
    justify-content: center;
    align-items: center;
}

.ovm-content-grid{
    
    height: 80vmin;;
    width: 80vmin;
    
    display: grid;
    grid-template-columns: auto auto;
    align-items: center;
    justify-items: center;
}
.ovm-content-grid > .link{

   width: 80%;
   height: 80%;
   border-radius: 8px;
   text-align: center;
   font-family: 'Raleway', sans-serif;
}


 .ovm-content-grid > .link {
    text-decoration: none;
    color: var(--color)
}

 .ovm-content-grid > .link:nth-child(1){
    background-color: var(--theme-ui-colors-green);
}


 .ovm-content-grid > .link:nth-child(2){
    background-color: var(--theme-ui-colors-purple);
}

 .ovm-content-grid > .link:nth-child(3){
    background-color:var(--theme-ui-colors-yellow)
}
 .ovm-content-grid > .link:nth-child(4){
    background-color: var(--theme-ui-colors-pink)
}



 .ovm-content-grid > .link >.icon{
    width: 50%;
    height: 50%;
    padding-top: 13%;   
    fill: black;
}

 .ovm-content-grid > .link:hover{
    background-color: transparent; 
}
 .ovm-content-grid > .link:nth-child(1):hover >  .icon {
    fill: var(--theme-ui-colors-green); 
}
 .ovm-content-grid > .link:nth-child(2):hover > .icon {
    fill: var(--theme-ui-colors-purple)
}
 .ovm-content-grid > .link:nth-child(3):hover >  .icon {
    fill: var(--theme-ui-colors-yellow)
}
 .ovm-content-grid > .link:nth-child(4):hover >  .icon {
    fill: var(--theme-ui-colors-pink)
}


.oc-close{
   position: absolute;
    width: 30px;
    height: 30px;
    float: right;
    margin:3%;
    padding: 5px;
    right:0%;
    top:0%;
    z-index: 4 ;
  
}

.oc-close > .icon {
    width: 30px;
    height: 30px;
  
}
.oc-close > .icon > g > g > path {
    fill: var(--color)
}


@media screen and (max-width: 800px) {
    
 
     .ovm-content-grid > .link{
    margin: 0 auto;
    width: 35vmin;
    height: 35vmin;
    
    } 
    .ovm-content-grid > .link > h4{
        margin:5%;
    }

  

}



.th-selector{
    margin: 0 10px;
    padding: 0; 
    border:none;
    background:none;
    outline:none
}


.icon.light{
  fill:var(--color);

}

.icon{
   height :32px;
   width :  32px;
   fill:var(--color);
}

.icon.hide{
  display: none;
}

.header-hm{
    position: fixed;
   height: 5%;
   width: 96%;  
   z-index: 2; 
   display: flex;
   flex-direction: row-reverse;
   padding: 3% ;
}

   .hm-hb1{
 position: relative;
float: right;
  
    width: 50px;
    height: 50px;
  
}

.header-hm > .hm-hb1 > .icon{

    fill: var(--color);
    width: 30px;
    height: 30px;
}


