
.credits{
    position: absolute;
    right: 0;
    padding-right: 0;
    border-bottom-left-radius: 5px;
   
    top: 30vh;
    height: fit-content;
    /* padding-bottom: 1%; */
    width: 330px;
    z-index: 3;
    transition: 0.7s;
    transition-timing-function: ease-in-out;
    background-color: var(--bgl);
  }
  
  
  
  .credits.hide{
    width: 0px;  
  }
  
  .credits.unhide{
    width: 330px;
  }
  
  .credits > .hb1{
    
    position: absolute;
    font-family: 'Teko', sans-serif;
    background-color: var(--bgl);
  
    z-index: 3;
    width: 100px;
    height: 35px;
    
    left:0;
    text-align: center;
    transform: rotate(-90deg); 
    transform-origin: 0% 0%;
    font-size: 20px;
    border-top-right-radius: 5px;
    border-top-left-radius: 5px;
    margin-left: -40px;
    padding-top: 5px;
    margin-top: 100px;
    letter-spacing: 0.8px;
    font-size: calc(15px + 1vmin); 
  }

  .credits > .hb1:hover{
      color: var(--text-highlight)
  }

  .credits-content{
    font-family: 'Inconsolata', monospace;
    width: 300px;
    height: 10em;;
   padding-left:20px;
    margin-top: 30px;
    
    border-bottom-left-radius: 8px;

    line-height: 1.5em;
    text-align: left;
    font-size: calc(8px + 1vmin);      
    z-index: 3;
    background-color: var(--bgl);
}

.credits-content > a{
    text-decoration: none;
    color: lightblue;
}


.credits-content > span{
    display: none;   

}




.credits-content >  a:hover{
    color: var(--theme-ui-colors-cyan)
 
}


@media screen and (max-width: 800px){
    .credits{
        top: 50vmax;
        height:30vmax;
        margin-right: 0px;
    }

    .credits.unhide{
        width: 80vw;
      }

    .credits > .hb1{
        width: 100px;
        height: 35px;
        font-size: calc(15px + 2vmin); 
        margin-left: -38px;
        padding-top: 5px;
       
    }

    .credits-content{
        width: 80vw;
    margin-top:  10vw;
    margin-left: 0;
    padding-left: 15px;
    font-size: calc(11px + 1vmin);
    z-index: 3;
    }

}