html {
  scroll-behavior: smooth;
}

.fullStack > span:nth-child(1) {
	font-size: 0px;
	animation: roll1 ease-in-out 6s infinite;
}

.fullStack > span:nth-child(2) {
	font-size: 0px;
	animation: roll2 ease-in-out 6s infinite;

}

.fullStack > span:nth-child(3) {
  animation: roll3 ease-in-out  6s infinite;
}


@keyframes roll1 {
  0%, 30%{
		opacity: 1;
		font-size: inherit
	}
	30%, 60%{
		opacity: 0;
		font-size: 0
	}
	60%, 100%{
		opacity: 0;
		font-size:0px;
	}
}

@keyframes roll2 {
	0%, 30%{
		font-size:0px;
		opacity: 0;
	}
  30%, 60%{
		opacity: 1;
		font-size: inherit
	}
	60%, 100%{
		opacity: 0;
		font-size:0px;
	}
}

@keyframes roll3 {
	0%, 30%{
		opacity: 0;
		font-size:0px;
	}
  30%, 60%{
		opacity: 0;
		font-size:0px;
	}
	60%, 100%{
		opacity: 1;
		font-size:inherit
	}
}

.flicker:hover{
  animation: upper 2s linear;
}


@keyframes upper {
  0%, 30% {
		
    opacity:.99; 
  }
  30%, 60% {
    opacity:0.5; 
    font-size: 70px;
    color: yellow;

  }
  60%, 100%{
		font-size:inherit;
		text-shadow:inherit;
		opacity:1;
  }
}
 
.b2{
	position: absolute;
	width:25px;
	height:25px;
	margin: auto;
	bottom:5%;
	transform-origin: bottom; 
}



.b2 > g > g > path{
	fill: var(--color)
}








