

.sk-1{  
  position: relative;
  width:100%;
  height: auto;

 
} 

.sk-header{
  position: relative;
}

.sk-h1{
  position: absolute;
  margin: 0px;
  top: 90%;
  z-index: 2;
  margin-left: 2%; 
  
  transform: rotate(-90deg); 
  font-family: 'Teko', sans-serif;
  font-size: calc(35px + 2vmin); 
  /* text-shadow: black; */
  letter-spacing: 1px;
 
}

.sk-header{
 
 height: 20vh;
 top:0px;


}

.sk-content{
 position: relative;
 /* top: 20vh; */
  width: 100%;
  height:75vh;
  background-color: var(--bgl);
  display: flex;
  flex-direction: row;
}

.sk-footer{

  width: 100%;
  height: 5vh;
  top:95vh;
  background-color: var(--bg);
  
}

.sk-lc1{
  position: relative;
  
  padding-top: 2%;
  width: 50%;
  height:40vh;
  margin:0 8%;
  display: flex;  
  flex-flow: wrap;
  flex-direction: row;
  align-content: center;
}



.sk-lc1 > span {

  align-self: center;
  margin: 4px;
  padding: 1.25em;
  background-color: var(--bg);
  border-radius: 50px;
  /* height:4px;       */
  font-family: 'Inconsolata', monospace;
  letter-spacing: 1px;
  line-height: 2px;
  
}



.sk-rc1{
 
  float: right;
  width: 30vw;
  height: auto;
  margin: 2%;
  margin-right:5vw;
  margin-left: 0px;
  padding-top: 7%;
  font-family: 'Teko', sans-serif;
  letter-spacing: 1px;
  border-radius: 4px;
  text-align: left;
  
}



.sk-rc1 > h1{
  width: 80%;
  margin-left: 20%;
}



.resume-sec{
  position: absolute;
  bottom: 0px;;
  width: 50vw;;
  height:30vh;
  margin-left:9%;
  
  letter-spacing: 1px;
  text-align: left;
  font-family: 'Inconsolata', monospace;
  font-weight: 600;
}

.cv-download{
  position: absolute;
  text-align:center;
  left: 15vw;
  width: 15vw;
  height: 30px;
  padding-top: 10px;
 
  border: 1px solid;
  text-decoration: none;
  color: var(--color);
  letter-spacing: 1px;
  
  font-weight: 100;

  
}





.cv-download:hover{
  color: var(--text-highlight);
  /* text-shadow: black; */
  background-color: var(--bg);
  border: 1px solid var(--bg);
}


/* Responsive layout - when the screen is less than 800px wide, make the two columns stack on top of each other instead of next to each other */
@media screen and (max-width: 800px) {
  /* .sk-lc1 {
    width: 80vw;
    height: 50vh;
    
    
    margin-left: 30vw;
    margin-top: 0px;
   
      
  

  } */

  .sk-content{
    width: 100%;
    height:auto;
    flex-direction: column;
    
  }

  .sk-lc1{
    position: relative;
    height: auto;
    width: 75vw;
    margin-left: calc((38px + 2vmin ) * 2) ;
    margin-right: 5%;
    margin-top: calc(30px + 2vmin);
    margin-bottom: 5%;
    font-size: 15px;
  }

  .sk-lc1 > span {

    align-self: center;
  
    


    align-self: center;
    margin: 4px;
    padding: 1em;
   
    /* height:4px;       */
    font-family: 'Inconsolata', monospace;
    letter-spacing: 1px;
    line-height: 2px;
 
     
    
  }

  .sk-rc1{
    position: relative;
  
    width: 80vmin;
    height:auto;
    margin: 10%;
    padding-top: 4%;
    /* margin-right: 15vmin;
    margin-bottom: 0px; */
    padding: 0;
    font-family: 'Teko', sans-serif;
    letter-spacing: 1px;
    border-radius: 4px;
  }
  
  .sk-rc1 > h1{
   position: relative;
   margin: 0;
   /* padding: 0; */
   /* padding-left: 4%;
    top:-1.5em; */
    margin-left: 5px;
    margin-bottom:0px;
    width: 100%;
    text-align: left
  }
  .resume-sec{
    position: absolute;
    bottom: -40vh;
    width: 80%; 
    z-index: 4;
    display: none;
  }

  .cv-download{

    left: 15vw;
    width: 60vw;
    height: 25px;
    
  }

  .cv-download:hover{
    color: var(--text-highlight);
    text-shadow: #000;
    background-color: var(--bgl);
  
    border: 1px solid var(--bgl);
  }
  
}