
.header-hm{
    position: fixed;
   height: 5%;
   width: 96%;  
   z-index: 2; 
   display: flex;
   flex-direction: row-reverse;
   padding: 3% ;
}

   .hm-hb1{
 position: relative;
float: right;
  
    width: 50px;
    height: 50px;
  
}

.header-hm > .hm-hb1 > .icon{

    fill: var(--color);
    width: 30px;
    height: 30px;
}


/* TODO: extract common properties */
.overlay-content-active {
    height: 100vh;
    width: 100vw;
    position: fixed;
    z-index: 4 ;
    top: 0;
    left: 0;
    opacity: 0.95;
    background-color: var(--bg);
    overflow-x: hidden;
    transition: 0.5s;
    animation-timing-function: ease-in-out
  }

  .overlay-content-hide {
    height: 100vh;
    width: 0;
    position: fixed;
    z-index: 4 ;
    top: 0;
    left: 0;
    opacity: 0.9;
    background-color: var(--bg) 0.9;
    overflow-x: hidden;
    transition: 0.5s;
  }